"use client";

import { useSearchParams } from "next/navigation";
import { useState } from "react";
import { destinations } from "../data/destinations";

export default function ReservationPage() {
  const searchParams = useSearchParams();
  const selectedFromCard = searchParams.get("package") || "";

  // ✅ Initialize destination from URL directly — ESLint-safe
  const [formData, setFormData] = useState({
    title: "",
    name: "",
    email: "",
    phone: "",
    travelDate: "",
    travelers: "1",
    destination: selectedFromCard,
    notes: "",
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>
  ) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    alert("Reservation submitted! (Backend integration pending)");
  };

  return (
    <main className="min-h-screen flex flex-col items-center bg-gradient-to-b from-sky-50 to-white py-20 px-6">
      <div className="w-full max-w-3xl bg-white rounded-2xl shadow-2xl p-10 border border-gray-100">
        <h1 className="text-4xl font-extrabold text-sky-700 text-center mb-6">
          Plan Your Dream Journey
        </h1>
        <p className="text-center text-gray-600 mb-10 text-lg">
          Fill in the details below and let us craft the perfect travel experience for you.
        </p>

        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Title */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Title</label>
            <input
              type="text"
              name="title"
              value={formData.title}
              onChange={handleChange}
              placeholder="e.g., Mr, Ms, Dr..."
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Full Name */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Full Name</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              placeholder="Your full name"
              required
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Email */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Email</label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="your@email.com"
              required
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Phone */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Phone</label>
            <input
              type="tel"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="+94 71 234 5678"
              required
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Destination Dropdown */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Destination</label>
            <select
              name="destination"
              value={formData.destination}
              onChange={handleChange}
              required
              className="w-full px-4 py-2 border rounded-md bg-white focus:ring-2 focus:ring-sky-500 outline-none transition"
            >
              <option value="">Select a destination</option>
              {destinations.map((d) => (
                <option key={d.id} value={d.cityName}>
                  {d.cityName} — {d.tagline}
                </option>
              ))}
            </select>
          </div>

          {/* Travel Date */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Travel Date</label>
            <input
              type="date"
              name="travelDate"
              value={formData.travelDate}
              onChange={handleChange}
              required
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Travelers */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Number of Travelers</label>
            <input
              type="number"
              name="travelers"
              min="1"
              value={formData.travelers}
              onChange={handleChange}
              required
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition"
            />
          </div>

          {/* Notes */}
          <div>
            <label className="block text-gray-700 font-semibold mb-2">Special Requests / Notes</label>
            <textarea
              name="notes"
              value={formData.notes}
              onChange={handleChange}
              rows={4}
              placeholder="Any special requirements or preferences?"
              className="w-full px-4 py-2 border rounded-md focus:ring-2 focus:ring-sky-500 outline-none transition resize-none"
            ></textarea>
          </div>

          {/* Submit */}
          <button
            type="submit"
            className="w-full py-3 bg-sky-600 text-white font-semibold rounded-lg shadow-lg hover:bg-sky-700 transition-all duration-300 transform hover:-translate-y-0.5 hover:scale-[1.02]"
          >
            Submit Reservation
          </button>
        </form>
      </div>
    </main>
  );
}
